<template>
  <div>
    <h1>STORE</h1>
    <input id="search" placeholder="Search" v-model="search" v-on:change="search">
    <div
      v-if="modules !== null"
      style="display: flex;flex-wrap: wrap;">
      <a
        v-for="module in modules"

        :key="module.id"
        class="single-module"
        @click="switchComponent('singleModule', module.ID)">
        <div
          style="font-size: 25px;font-weight: bold;line-height: 25px;word-wrap: break-word;text-overflow: ellipsis;white-space: nowrap;overflow: hidden">
          {{ module.title }}
        </div>
        <p
          style="font-size: 14px;
    margin-top: 15px;
    overflow: hidden;
    height: 42px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical; ">
          {{ module.description }}
        </p>
        <p>
          <i
            v-for="i in parseInt(module.rating)"
            :key="i"
            class="fas fa-star" />
          <i
            v-for="i in range(parseInt(module.rating) + 1, 5)"
            :key="i"
            class="far fa-star" />
        </p>
      </a>
    </div>
    <keyboard
      :text.sync="search"
      :input="'search'"/>
  </div>
</template>

<script>
import axios          from 'axios'
import { mapGetters } from 'vuex'

export default {
  name: 'ListingModule',
  components: {},
  props: {},

  data () {
    return {
      modules: null,
      token: null,
      search: '',
    }
  },
  computed: mapGetters(['getCurrentProfile']),
  mounted () {

    axios.post('https://api.dev.ureflect.io/v1/signin', {
      email: 'matthieu@eip.fr',
      password: 'toto42sh'
    }).then((response) => {
      this.getModules(response)
    })
  },
  watch: {
    search (val) {
      this.search = val

      const instance = axios.create({
        baseURL: 'https://api.dev.ureflect.io/v1',
        timeout: 1000,
        headers: {'x-access-token': this.token}
      })
      instance
        .get('/modules')
        .then((response) => {
          let modules = response.data.data
          let modules_final = []

          if (modules) {
            for (let i = 0; i < modules.length; i++) {
              if (modules[i].title.toLowerCase().includes(this.search.toLowerCase())) {
                modules_final.push(modules[i])
              }
            }

            this.modules = modules_final
          }
        })
    }
  },
  methods: {
    getModules: function (response) {
      this.token = response.data.data.token

      const instance = axios.create({
        baseURL: 'https://api.dev.ureflect.io/v1',
        timeout: 1000,
        headers: {'x-access-token': this.token}
      })
      instance
        .get('/modules')
        .then((response) => {
          this.modules = response.data.data
          console.log(this.modules)
        })
    },
    range: function (min, max) {
      var array = [],
        j = 0
      for (var i = min; i <= max; i++) {
        array[j] = i
        j++
      }
      return array
    },
    switchComponent (comp, module_id) {
      this.$emit('switchComp', [comp, module_id])
    },
    loadModuleView: function () {
    }
  },
}
</script>

